import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { StyledBody } from 'baseui/card';
import { StyledCard } from '..';
import { Block } from 'baseui/block';
import {
  Bar,
  BarChart,
  Tooltip,
  XAxis,
  YAxis,
  ResponsiveContainer,
} from 'recharts';
import Loader from './Loader';

import { useData } from '../../contexts/DataContext';
import groupBy from 'lodash.groupby';
import { sum } from '../../helpers/misc';
import { useStyletron } from 'baseui';

const REGISTERED_KEY = 'registered';
const VOTES_KEY = 'votes';
const NSEATS_KEY = 'national_seats';

function accumulateData(data) {
  return Object.entries(groupBy(data, 'party'))
    .map(([party, dataPerParty]) => {
      const registeredCount = sum(
        dataPerParty.filter((el) => el.key === REGISTERED_KEY)
      );
      const votesCount = sum(
        dataPerParty.filter((el) => el.key === VOTES_KEY)
      );
      const nSeatsCount = sum(
        dataPerParty.filter((el) => el.key === NSEATS_KEY)
      );

      return {
        party,
        [REGISTERED_KEY]: registeredCount,
        [VOTES_KEY]: votesCount,
        [NSEATS_KEY]: nSeatsCount,
      };
    })
    .sort((a, b) => b[[REGISTERED_KEY]] - a[[REGISTERED_KEY]]);
}

export default function PartiesSplit() {
  const { t } = useTranslation();
  const {     registered,      votes,      national_seats, isLoading } = useData();
  const [groupedData, setGroupedData] = useState(null);
  const [css, theme] = useStyletron();
  const { setClickedParty } = useData();

  useEffect(() => {
    const preparedCases = registered
      ? registered
          .filter((el) => el.party)
          .map((el) => ({ ...el, key: REGISTERED_KEY }))
      : [];
    const preparedDeaths = votes
      ? votes
          .filter((el) => el.party)
          .map((el) => ({ ...el, key: VOTES_KEY }))
      : [];
    const preparedCures = national_seats
      ? national_seats
          .filter((el) => el.party)
          .map((el) => ({ ...el, key: NSEATS_KEY }))
      : [];

    setGroupedData(
      accumulateData([...preparedCases, ...preparedDeaths, ...preparedCures])
    );
  }, [registered, votes, national_seats]);

  return (
    <StyledCard
      style={($theme) => ({
        [$theme.mediaQuery.medium]: {
          maxHeight: 'calc(100vh - 200px)',
          overflow: 'auto',
        },
        [$theme.mediaQuery.large]: {
          width: '380px',
        },
      })}
    >
      <StyledBody>
        <Block
          $style={{
            margin: '12px 0 20px',
            overflow: 'hidden',
          }}
        >
          {isLoading && <Loader />}
          {groupedData && (
            <ResponsiveContainer height={groupedData.length * 26} width={'99%'}>
              <BarChart
                data={groupedData}
                layout='vertical'
                className={css({
                  fontSize: '12px',
                })}
              >
                <YAxis
                  dataKey='party'
                  type='category'
                  tick={{
                    fill: theme.colors.contentPrimary,
                    cursor: 'pointer',
                  }}
                  width={130}
                  onClick={({ value }) => setClickedParty(value)}
                />
                <XAxis type='number' hide />
                <Tooltip
                  contentStyle={{
                    backgroundColor: theme.colors.backgroundPrimary,
                    borderColor: theme.colors.backgroundTertiary,
                  }}
                  cursor={{ fill: theme.colors.backgroundTertiary }}
                />
                <Bar
                  name={t('registeredShort')}
                  stackId={'a'}
                  dataKey={REGISTERED_KEY}
                  fill={theme.colors.negative}
                />
                <Bar
                  name={t('votes')}
                  stackId={'a'}
                  dataKey={VOTES_KEY}
                  fill={theme.colors.primary}
                />
                <Bar
                  name={t('national_seats')}
                  stackId={'a'}
                  dataKey={NSEATS_KEY}
                  fill={theme.colors.positive}
                />
              </BarChart>
            </ResponsiveContainer>
          )}
        </Block>
      </StyledBody>
    </StyledCard>
  );
}
