import {
    STATE_FLAGS,
} from '../constants';

export const getStateFlag = (name) => {
    for (const [key, value] of Object.entries(STATE_FLAGS)) {
        if (value === name) {
            return key;
        }
    }
};

export const capitalize = (s) => {
    if (typeof s !== 'string') return '';
    return s.charAt(0).toUpperCase() + s.slice(1);
};

