const MIN_MARKER_SIZE = 90;
const MAX_MARKER_SIZE = 180;

const Centered = styled('div', ({$theme}) => ({
    backgroundColor: $theme.colors.backgroundPrimary,
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    flexDirection: 'column',
    height: 'auto',
    width: '100vw',
    [$theme.mediaQuery.medium]: {
        height: '100vh',
    },
}));

function createMarkerIcon(size, registrationsCount, votesCount, npSeatsCount, rpSeatsCount) {
    return divIcon({
        iconSize: [size, size],
        html: renderToStaticMarkup(
            <MarkerIcon
                size={size}
                registrationsCount={registrationsCount}
                votesCount={votesCount}
                npSeatsCount={npSeatsCount}
                rpSeatsCount={rpSeatsCount}
            />
        ),
    });
}

function getMarkerSize(max, count) {
    return MIN_MARKER_SIZE;
}

function getLocationForStation(clickedStation, data) {
    return data
        .filter((item) => item.stations.name === clickedStation)
        .pop().stations.location;
}

function renderTooltip(props, message) {
    return (
        <Tooltip style={{fontFamily: "Noto Sans Ethiopic, Rubik", fontSize: "12px"}} id="button-tooltip" {...props}>
            {message}
        </Tooltip>
    );
}

